/*eslint-env amd*/
/*eslint camelcase:0*/
define('app/views/body',["underscore", "backbone", "jquery", "app/models", "hbs!views/admin/partials/alert"], function(_, Backbone, $, models, alertTemplate) {
    "use strict";
    var BodyView = Backbone.View.extend({
        el: "body",
        collection: models.collections.states,
        views: [],
        updateState: function() {
            var state = this.collection.get("active");
            //console.log("Update state", state, this.collection.length)
            if (state) {
                this.$content.find("#alert").remove();
                this.$el.removeClass("bg-danger");

                if (!state.get("active")) {
                    this.$el.addClass("bg-danger");
                    this.$content.prepend(alertTemplate({
                        name: "L'opération n'est pas en cours",
                        message: _.last(state.get("message"))
                    }));

                    return;
                }

            }
        },
        initialize: function() {
            this.$content = $("#content");
            this.listenTo(this.collection, "all", this.updateState.bind(this));

            this.render();
            this.updateState();
        },
        add: function(view) {
            this.$content.append(view.$el);
        },
        render: function() {
            return this;
        }

    });

    return new BodyView();
});
