/*eslint-env amd*/
/*eslint camelcase:0*/
define('app/views/control/modules',["hbs!views/admin/partials/control/modules", "app/views/control/module", "underscore", "backbone", "app/views/utils"], function(template, ModuleView, _, Backbone, utils) {
    "use strict";
    var ModulesView = utils.ParentView.extend({
        tagName: "div",
        initialize: function(data) {
            this.type = data.type;
            this.listenTo(this.collection, "sync", this.render);
            this.listenTo(this.collection, "reset", this.render);
            this.render();
        },
        template: template,
        render: function() {
            var self = this;
            this.close();
            var filtered = this.collection.filter(function(model){
                return model.get("type") === self.type;
            });

            var counts = _.countBy(filtered, function(model) {
                return model.get("type");
            });

            counts.hasModule = !!filtered.length;
            counts.numModules = filtered.length;
            counts.connected = _.filter(filtered, function(model){
                return model.get("is_connected");
            }).length;
            counts.disconnected = counts.numModules - counts.connected;

            counts.type = this.type;

            this.$el.html(this.template(counts));

            _.each(filtered, function(module) {
                var moduleView = new ModuleView({
                    model: module,
                    type : this.type
                });

                this.addChildView(moduleView);

                this.$el.find(".modules").append(moduleView.render().$el);
            }, this);
            return this;
        }

    });

    return ModulesView;
});
