define('app/models',["app/collections/modules", "app/collections/settings", "app/collections/states", "app/collections/week", "underscore", "backbone", "backbone.io"], function(ModulesCollection, SettingsCollection, StatesCollection, Week, _, Backbone) {
    "use strict";
    var io = Backbone.io.connect();

    var models = {
        collections: {
            modules: new ModulesCollection([], {
                parse: true
            }),
            settings: new SettingsCollection([], {
                parse: true
            }),
            week: new Week([], {
                parse: true
            }),
            states: new StatesCollection([], {
                parse: true
            })
        }
    };

    function fetchAll() {
        _.each(models.collections, function(collection) {
            collection.fetch();
        });
    }

    io.on("connect", function() {
        console.info("Socket connection");
        fetchAll();
    });

    io.on("reconnect", function() {
        console.info("Socket reconnection");
        fetchAll();
    });

    io.on("disconnect", function() {
        console.info("Socket disconnection");
        models.collections.modules.reset();
    });

    return models;
});
