define('views/helpers/formatDate',["hbs/handlebars", "moment"], function(Handlebars, moment) {
    "use strict";
    function formatDate(context, options) {
        return moment(parseInt(context, 10)).format(options.hash.format || "[Le] Do MMMM YYYY [à] HH:mm:ss");
    }
    Handlebars.registerHelper("formatDate", formatDate);
    return formatDate;
});
